*, *::before, *::after {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

body {
  font-family: 'Raleway', sans-serif;
  font-style: normal;
  letter-spacing: 0;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-feature-settings: "liga" on;
  text-transform: uppercase;
}

h2{
  padding: 2%;
  text-align: center;
  color: #9d9897;;
  background: #fff;
  word-spacing: 7px;

}
h4{
  margin: 7px 0px;
}
.btn {
  background-color: white;
  border: 1px solid #777;
  border-radius: 1rem;
  color: #9d9897;
  
  padding: 0.5rem;
  text-transform: lowercase;
}
a{
  text-decoration: none;
}
.btn--block {
  display: block;
  width: 100%;
}
.Main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background: linear-gradient( #c3d7df8c,#7f989d);
}
.cards {
  
  flex: 0 0 470px;
  padding: 7%;
  
} 

@media (max-width:450px){
  .cards {
  
    flex: 0 0 300px;
    padding: 7%;
    
  } 
}

.cards img {
 
  width: 100%;
  height: 190px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  
}


.card {
  border-radius:10px ;
  position: relative;
  background-color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 1s;
  /* width: %; */
}



.container {
  padding: 7px 16px;
}

.cards .card_info > button {
  background: gray;
  border: 0;
  color: white;
  padding: 10px;
  width: 100%;
  }
  .card_category{
    font-size: 12px;
    color: #9d9897;
  }

  .card:hover {
    top: -2px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }